<template>
  <div class="answer" id="answer">
    <div id="content">
      <div id="sen">
        <img :src="user.headimgurl">
      </div>
    </div>
    <div id="tab">
      <router-link to="/balance"><div id="btn">我的余额<p>{{user.bonus}}</p></div></router-link>
      <router-link to="/sc"><div id="btn">提现设置<p>设置收款账号</p></div></router-link>
      <!-- <div id="btn">答题记录<p>闯关答题记录</p></div> -->
      <router-link to="/rd"><div id="btn">复活卡（邀请得卡）<p>{{user.resurrection}}张</p></div></router-link>
    </div>
    <div class="nav">
      <div class="left">
        <router-link to="/index">       
          <img  src="../assets/run.png">
          <p>进行中</p>
        </router-link>
      </div>
      <div class="right">
        <router-link to="/center">   
          <img src="../assets/my.png">
          <p>我的</p>
         </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      user: {}
    }
  },
  methods: {
    async getUserInfo () {
      let opt = {
        method: 'get',
        url: this.$url + '/api/user',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.user = res.data.data
      }
    }
  },
  async created () {
    this.getUserInfo()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #content {
    width: 100%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 10px;
    height: 120px;
    border-radius: 8px;
    position: relative;
    padding-top: 10px;
  }
  #sen {
    position: absolute;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    background-color: #1E1344;
    margin: auto; 
    right: 0;
    left: 0;
    bottom: -20px;
    padding: 5px;
    z-index: 999;
  }
  #sen img{
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
  #tab {
    width: 100%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 10px;
    padding: 15px 12px 60px; 
    border-radius: 8px;
    position: relative;
  }
  #btn {
    background-color: #4B4269;
    width: 100%;
    text-align: left;
    margin:0 auto;
    height: 30px;
    margin-top: 20px;
    border-radius: 18px;
    line-height: 30px;
    font-size: 14px;
    color: #fff;
    position: relative;
    padding: 0 10px;
  }
  #btn p {
    position: absolute;
    right: 10px;
    top: 0px;
    color: #FB306B;
    margin: 0;
    bottom: 0;
    line-height: 30px;
  }
  .nav {
    height: 80px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    padding-top: 5px;
  }
  .nav .left, .right{
    width: 50%;
    text-align: center;
    line-height: 0px;
    color: #FB306B;
  }
  .nav img{
    width: 50px;
    height: 50px;
  }
</style>



