import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index'
import answer from '@/pages/answer'
import success from '@/pages/success'
import result from '@/pages/result'
import center from '@/pages/center'
import balance from '@/pages/balance'
import resurrectionCard from '@/pages/resurrectionCard'
import code from '@/pages/code'
import setCode from '@/pages/setCode'
import page from '@/pages/page'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'page',
      component: page
    },
    {
      path: '/index',
      name: 'index',
      component: index
    },
    {
      path: '/answer',
      name: 'answer',
      component: answer
    },
    {
      path: '/success',
      name: 'success',
      component: success
    },
    {
      path: '/result',
      name: 'result',
      component: result
    },
    {
      path: '/center',
      name: 'center',
      component: center
    },
    {
      path: '/balance',
      name: 'balance',
      component: balance
    },
    {
      path: '/rd',
      name: 'rd',
      component: resurrectionCard
    },
    {
      path: '/code',
      name: 'code',
      component: code
    },
    {
      path: '/sc',
      name: 'sc',
      component: setCode
    }
  ]
})



