<template>
  <div class="answer" id="answer">
    <div id="content">
      <img id="banner" src="../assets/banner@3x.png">
    </div>
    <div id="code">
      <img  v-if="ticket!=''" id="banner" :src="'https://mp.weixin.qq.com/cgi-bin/showqrcode?ticket='+ticket">
    </div>
    <div id="btn_tx">关注微信，参与答题赢红包大奖</div>
  </div>
</template> 

<script>
export default {
  data () {
    return {
      ticket: ''
    }
  },
  methods: {
    async getData () {
      console.log('---------getData---------------')
      let opt = {
        method: 'get',
        url: this.$url + '/api/user/qrcode',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      console.log('==============res=============', res)
      if (res.data.code === 0) {
        this.ticket = res.data.data
      }
    }
  },
  async created () {
    this.getData()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #content {
    width: 100%;
    margin:0 auto;
    height: 120px;
    border-radius: 8px;
  }
  #content img {
    width: 100%;
    height: 100%;
    border-radius: 8px;
  }
  #code {
    width: 150px;
    height: 150px;
    margin:0 auto;
    margin-top: 100px;
  }
  #code img {
    width: 100%;
    height: 100%;
  }
   #btn_tx {
    width: 240px;
    height: 28px;
    margin:0 auto;
    margin-top: 150px;
    border-radius: 20px;
    color: #fff;
    font-size: 14px;
    line-height: 28px;
    text-align: center;
    background-color: #FB306B;
   }
</style>



