<template>
  <div class="answer" id="answer">
    <div id="content">
      <div id="title">我的余额</div>
      <div id="bonus">{{balance}}</div>
      <div id="tip">最低提现金额50元</div>
    </div>
    <div id="btn_tx" @click="showTx = true">申请提现</div>
    <div id="list">
      <div id="item" v-for="(item, index) in record" :key="index">
        <div id="left">
          <span style="vertical-align: super;">{{item.createdAt | formatTimeContent}}</span>
        </div>
        <div id="middle">{{item.account}}</div>
        <div id="right" v-if="item.result = 1">等待到账</div>
        <div id="right" v-else-if="item.result = 2">提现成功</div>
        <div id="right" v-else>提现失败</div>
      </div>
    </div>
    <div id="btn"><router-link to="/center">返回个人中心</router-link></div>
    <div>
      <mu-dialog :open="showTx" title="提现" @close="showTx">
        <mu-auto-complete hintText="请输入提现金额" v-model="account"/>
        <mu-flat-button slot="actions" @click="showTx = false" primary label="取消" />
        <mu-flat-button slot="actions" primary @click="tx" label="确定"/>
      </mu-dialog>
    </div>
    <div>
      <mu-dialog :open="dialog" >
        {{msg}}
       <mu-flat-button label="确定" slot="actions" primary @click="dialog =false"/>
      </mu-dialog>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      record: [],
      balance: 0,
      showTx: false,
      msg: '',
      account: 0
    }
  },
  methods: {
    async getBalance () {
      let opt = {
        method: 'get',
        url: this.$url + '/api/user/balance',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.record = res.data.data
        this.balance = res.data.balance
      }
    },
    async tx () {
      this.showTx = false
      let res = await this.$http.post(this.$url + '/api/user/tx', {
        account: this.account,
        openid: sessionStorage.getItem('openid')
      })
      if (res.data.code === 0) {
        this.msg = '提现申请成功，请耐心等待到账'
        this.dialog = true
        this.getBalance()
      } else {
        this.msg = res.data.msg
        this.dialog = true
      }
    }
  },
  async created () {
    this.getBalance()
  },
  filters: {
    formatTimeContent (val) {
      if (val) {
        if (val.toString().length < 13) {
          val = val * 1000
        }
        let time = new Date(val).toLocaleString(undefined, {hour12: false})
        // 把时间表达式中/改为-
        time = time.split('/').join('-')
        return time
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  .mu-auto-complete {
    margin: 0 auto;
    width:90%;
    padding: 0 20px;
  }
 .mu-text-field {
    width: 100% ! important;
  }
  #content {
    width: 100%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 10px;
    height: 120px;
    border-radius: 8px;
    position: relative;
    padding-top: 10px;
  }
 #title {
  font-size: 16px;
  color: #fff;
 }
 #bonus {
  font-size: 36px;
  color: #FB306B;
 }
 #tip {
  color: #fff;
 }
 #btn_tx {
  width: 132px;
  height: 28px;
  margin:0 auto;
  margin-top: 20px;
  border-radius: 20px;
  color: #fff;
  font-size: 14px;
  line-height: 28px;
  text-align: center;
  background-color: #FB306B;
 }
 #list {
  overflow-y: auto;
  position: absolute;
  top: 210px;
  bottom: 60px;
  left: 15px;
  right: 15px;
  border-top: 2px solid #f0f0f0;
 }
 #item {
  height: 50px;
  width: 100%;
  position: relative;
 }
 #left {
  float: left;
  text-align: left;
  color: #737373;
  padding: 10px 5px;
  padding-right: 35px;
 }
 #middle {
  float: left;
  padding: 10px 5px;
 }
 #right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: right;
 color: #737373;
  padding: 10px 5px;
 }
#btn {
  width: 150px;
  height: 35px;
  margin:0 auto;
  background-color: #1E1344;
  color: #fff !important;
  text-align: center;
  font-size: 16px;
  border-radius: 18px;
  margin-top: 75px;
  line-height: 35px;
  position: absolute;
  margin: auto; 
  right: 0;
  left: 0;
  bottom: 10px;
}
#btn a {
  color: #fff !important;
}
</style>



