<template>
  <div class="answer" id="answer">
    <div id="title">微信收款二维码</div>
    <div id="code">
      <img id="banner" ref='config' src="../assets/upload.png" @click="btnClick">
    </div>
    <br><br>
    <div id="title">微信收款二维码</div>
    <div id="code">
      <img id="banner" :src="this.$url+'/api/user/upload?code='+user.codeUrl">
    </div>
    <div id="tip">请设置您正在使用的微信/支付宝收款账户，一旦设置无法修改</div>
    <input type="file" id="file" class="file" @change="fileUpload" style="display:none">
  </div>
</template>

<script>
export default {
  data () {
    return {
      user: {},
      dialog: false,
      url: this.$url + '/api/user/upload'
    }
  },
  methods: {
    async getUserInfo () {
      let opt = {
        method: 'get',
        url: this.$url + '/api/user',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.user = res.data.data
      }
    },
    fileUpload (e) {
      let formdata = new FormData();
      formdata.append('file', e.target.files[0]);
      formdata.append('openid', sessionStorage.getItem('openid'))
      this.$http({
          url: this.$url + '/api/user/upload',
          method:'post',
          data: formdata,
          headers: {'Content-Type': 'application/x-www-form-urlencoded'}
      }).then((res)=>{
        this.getUserInfo()
      })
    },
    btnClick () {
      let eleNav =  document.getElementById('file')
      eleNav.click()
    }
  },
  async created () {
    this.getUserInfo()
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #title {
    color: #1E1344;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
  }
  #code {
    width: 150px;
    height: 150px;
    margin:0 auto;
    margin-top: 10px;
    border: 5px solid #1E1344;
    border-radius: 5px;
  }
  #code img {
    width: 100%;
    height: 100%;
  }
  #banner {
    width: 360px;
    height: 288px;   
  }
  #tip {
    width: 100%;
    text-align: center;
    color: #FB306B;
    font-size: 14px;
    margin-top: 80px;
  }
</style>



