<template>
  <div class="answer" id="answer">
    <div id="content">
      <div id="title">恭喜获得奖金</div>
      <div id="bonus">{{bonus}}</div>
      <div id="tip">奖金将在两个小时内到账，请耐心等候！</div>
    </div>
    <div id="txt">以下用户获得同等金额奖金：</div>
    <div id="list">
      <div id="item" v-for="(item, index) in result" :key="index">
        <div id="left"><img :src="item.headimgurl">
          <span style="vertical-align: super;">{{item.nickname}}</span></div>
        <div id="right">{{item.bonus}}</div>
      </div>
    </div>
    <div id="btn">返回个人中心</div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      result: [],
      bonus: 0
    }
  },
  methods: {
    open () {
      this.dialog = true
    },
    close () {
      this.dialog = false
    }
  },
  async created () {
    let gameId = this.$route.params.gameId
    if (gameId && typeof gameId !== 'undefined') {
      // 查询结果
      let opt = {
        method: 'get',
        url: this.$url + '/api/game/result/' + gameId
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.result = res.data.data
        this.bonus = this.result[0].bonus
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #content {
    width: 100%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 10px;
    height: 120px;
    border-radius: 8px;
    position: relative;
    padding-top: 10px;
  }
 #title {
  font-size: 16px;
  color: #fff;
 }
 #bonus {
  font-size: 36px;
  color: #FB306B;
 }
 #tip {
  color: #fff;
 }
 #txt {
  margin-top: 20px;
  color: #666;
  font-size: 16px;
  text-align: left;
  border-bottom: 2px solid #f0f0f0;
 }
 #list {
  overflow-y: auto;
  position: absolute;
  top: 190px;
  bottom: 60px;
  left: 15px;
  right: 15px;
  border-bottom: 2px solid #f0f0f0;
 }
 #item {
  height: 50px;
  width: 100%;
  position: relative;
 }
 #left {
  text-align: left;
  color: #1E1344;
  font-size: 16px;
  padding: 10px 5px;
 }
 #left img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
 }
 #right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: right;
  color: #FB306B;
  padding: 20px 5px;
 }
#btn {
  width: 150px;
  height: 35px;
  margin:0 auto;
  background-color: #1E1344;
  color: #fff;
  text-align: center;
  font-size: 16px;
  border-radius: 18px;
  margin-top: 75px;
  line-height: 35px;
  position: absolute;
  margin: auto; 
  right: 0;
  left: 0;
  bottom: 10px;
}
</style>



