<template>
  <div class="index" id="index">
    <div id="code" v-if="subscribe == 0">
      <img src="../assets/code1.png" >
      <div style="color: #1E1344;">关注后才可以参与答题赢红包哦</div>
    </div>
    <img id="banner" src="../assets/banner@2x.png">
    <div id="list">
      <div id="title">
        <div id="title-left"  :class="[selectIndex == 1 ? 'active' : '']" @click="getData(1)">进行中</div>
        <div id="title-right" :class="[selectIndex == 2 ? 'active' : '']" @click="getData(4)">已结束</div>
      </div>
      <div id="content">
        <div id="content-item" v-for="(item, index) in games" :key="index">
          <div id="content-item-left">
            <p class="text-nomal">开赛时间</p>
            <p class="content-time">{{item.startTime|formatTimeContent}}</p>
          </div>
          <div id="content-item-middle">
            <p class="text-nomal">本场奖金</p>
            <p style="color: #FB306B;font-size:30px">{{item.bonus}}
              <span style="color:#2A2A2A;font-size:20px;font-weight:500">元</span>
            </p>
          </div>
          <div id="content-item-right">
            <div v-if="item.status ==1 && item.apply!=item.limit && !item.appled" id="btn" @click="apply(item.id)">报名</div>
            <div v-else-if="item.status ==1 && item.appled" id="btn1">已报名</div>
            <div v-else-if="item.status==2" id="btn">进行中</div>
            <div v-else-if="item.apply==item.limit && !item.appled" id="btn1">已满</div>
            <div v-else id="btn1">已结束</div>
            <div id="number">人数 {{item.apply}}/{{item.limit}}</div>
          </div>
        </div>
      </div>
    </div>
    <div class="nav">
      <div class="left">
        <router-link to="/index">       
          <img  src="../assets/run.png">
          <p>进行中</p>
        </router-link>
      </div>
      <div class="right">
        <router-link to="/center">   
          <img src="../assets/my.png">
          <p>我的</p>
         </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      games: [],
      selectIndex: 1,
      openid: '',
      subscribe: 1
    }
  },
  methods: {
    async getData (status) {
      if (status === 1) {
        this.selectIndex = 1
      } else {
        this.selectIndex = 2
      }
      let opt = {
        method: 'get',
        url: this.$url + '/api/game',
        params: {
          status: status
        }
      }
      let res = await this.$http(opt)
      console.log(res.data)
      if (res.data.code === 0) {
        this.games = res.data.data
      }
    },
    async getWXOpenId () { // 查询微信openid，并且知道是否已经关注

    },
    async apply (id) { // 报名
      let res = await this.$http.post(this.$url + '/api/game/apply', {
        gameId: id,
        openid: this.openid
      })
      if (res.data.code === 0) {
        this.getData(1)
      }
    },
    close () {
      this.dialog = false
    }
  },
  async created () {
    sessionStorage.setItem('openid', 'oF8NMv1cLqoH5kNIeRQgkttjVLJI')
    let res = await this.$http.post(this.$url + '/wx/auth', {
      code: '111111'
    })
    console.log('.........................', res.data)
    let openid = res.data.openid
    this.openid = openid
    // 设置全局的openid
    // Vue.prototype.$openid = 'openid'
    // sessionStorage.setItem('openid', 'openid')
    let subscribe = res.data.subscribe
    this.subscribe = subscribe
    let turnDt = res.data.turnDt
    if (subscribe !== 0) {
      this.$socket.emit('login', {openid}, ack => {
      })     
    }
    if (turnDt && subscribe > 0) {
      this.$router.push({ name: 'answer', params: { openid: this.openid }})
    }
    // 跳转到答题页面
    this.$socket.on('start', msg => {
      this.$router.push({ name: 'answer', params: { openid: this.openid }})
    })
    await this.getData(1)
    // 为防止没收到开始答题 会在答题前后一分钟轮询是否要跳转到答题页面
    // TODO
    this.$socket.on('result', msg => {
      this.$router.push({ name: 'result', params: { gameId: msg.gameId }})
    })
  },
  filters: {
    formatTimeContent (val) {
      let time = new Date(val)
      let hour = time.getHours()
      let minutes = time.getMinutes()
      if (parseInt(minutes) < 10) {
        minutes = '0' + minutes
      }
      let seconed = time.getSeconds()
      if (parseInt(seconed) < 10) {
        seconed = '0' + seconed
      }
      return `${hour}:${minutes}`
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #index {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #code {
    text-align: center;
    width: 80%;
    height: 177px;
    margin: auto;  
    position: absolute;  
    top: 10%;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 999999;
    background-color: #fff;
    border-radius: 15px;
    border: 1px solid #f0f0f0;
    box-shadow: 0px 9px 15px #888888;
    color: #1E1344;
  }
  #code img{
    margin-top: 20px;
    width: 105px;
    height: 105px;
  }
  #banner {
    width: 100%;
    height: 127px;
  }
  #list {
    background-color: #1E1344;
    border-radius: 15px;
    padding: 5px 10px;
    position: absolute;
    top: 160px;
    left: 15px;
    right: 15px;
    bottom: 80px;
    overflow-y: auto;
  }
  #title {
    width: 100%;
    height: 45px;
    padding-top: 10px;
  }
  #title-left {
    float: left;
    color: #3F2C81;
    font-size: 18px;
    text-align: center;
    width: 50%;
    border-right: 1px solid #fff;
    height: 25px;
    line-height: 25px;
  }
  #title-right {
    float: left;
    color: #3F2C81;
    font-size: 18px;
    text-align: center;
    width: 49%;
    height: 25px;
    line-height: 25px;
  }
  #content {
    width: 100%;
    background-color: #fff;
    min-height: 500px;
    padding: 0 10px;
  }
  #content-item {
    height: 120px;
    border-bottom: 2px solid #f0f0f0;
    line-height: 26px;
  }
  .active {
    color: #fff !important;
  }
  #content-item-left {
    float: left;
    width: 25%;
    text-align: center;
  }
  #content-item p{
    margin: 0;
  }
  .text-nomal {
    margin-top: 30px !important;
    font-size: 12px;
    font-weight: bold;
    color: #2A2A2A;
  }
  .content-time {
    font-size: 25px;
    color: #FB306B;
  }
  #content-item-middle {
    float: left;
    width: 50%;
    text-align: center;
  }
  #content-item-right {
    float: left;
    width: 25%;
    text-align: center;
    position: relative;
  }
  #btn {
    width: 100%;
    height: 35px;
    border-radius: 25px;
    background-color: #FB306B;
    font-size: 14px;
    color: #fff;
    text-align: center;
    line-height: 35px;
    font-weight: bold;
    margin-top: 40px;
  }
  #btn1 {
    width: 100%;
    height: 35px;
    border-radius: 25px;
    background-color: #959595;
    font-size: 14px;
    color: #fff;
    text-align: center;
    line-height: 35px;
    font-weight: bold;
    margin-top: 40px;
  }
  #number {
    font-size: 12px;
    margin-top: 5px;
  }
  .nav {
    height: 80px;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    display: flex;
    padding-top: 5px;
  }
  .nav .left, .right{
    width: 50%;
    text-align: center;
    line-height: 0px;
    color: #FB306B;
  }
  .nav img{
    width: 50px;
    height: 50px;
  }
</style>



