<template>
  <div class="answer" id="answer">
    <router-link to="/index">首页</router-link><br>
    <router-link to="/answer">答题中</router-link><br>
    <router-link to="/success">闯关成功</router-link><br>
    <router-link to="/result">获奖结果</router-link><br>
    <router-link to="/center">个人中心</router-link><br>
    <router-link to="/balance">余额</router-link><br>
    <router-link to="/rd">复活卡</router-link><br>
    <router-link to="/code">分享</router-link><br>
    <router-link to="/sc">设置二维码</router-link>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false
    }
  },
  methods: {
    open () {
      this.dialog = true
    },
    close () {
      this.dialog = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #title {
    color: #1E1344;
    font-size: 16px;
    font-weight: bold;
    text-align: left;
  }
  #code {
    width: 150px;
    height: 150px;
    margin:0 auto;
    margin-top: 10px;
    border: 5px solid #1E1344;
    border-radius: 5px;
  }
  #code img {
    width: 100%;
    height: 100%;
  }
  #banner {
    width: 360px;
    height: 288px;   
  }
  #tip {
    width: 100%;
    text-align: center;
    color: #FB306B;
    font-size: 14px;
    margin-top: 80px;
  }
</style>



