<template>
  <div class="answer" id="answer">
    <div id="content">
      <div  v-if="time>0">
        <div id="sen">
          <div id="time">{{time}}</div>
        </div>
        <div id="question">{{qIndex}}、{{question}}</div>
        <div id="answer_div">
          <div class="tab" @click="answerFun(1)" :class="[selectIndex == 1 ? 'active' : '']">{{firstAnswer}}</div>
          <div class="tab" @click="answerFun(2)" :class="[selectIndex == 2 ? 'active' : '']">{{secondAnswer}}</div>
          <div class="tab" @click="answerFun(3)" :class="[selectIndex == 3 ? 'active' : '']">{{thirdAnswer}}</div>
        </div>
      </div>

      <div  v-else>
        <div id="question">{{question}}</div>
        <div  style="height: 150px; padding-top:30px">
          <div  v-if="showAnswer"  style="color: #F8306A">{{answerStr}}</div>
          <!-- <div v-if="showAnswer"  style="color: #F8306A">剩余人数： {{number}}</div> -->
        </div>
      </div>

      <div id="btn" @click="logout">退出比赛</div>
      <div id="fh"><img src="../assets/fh.png"> <span style="vertical-align: super;">复活卡：{{resurrection}}张</span></div>
      <div id="tip">复活卡仅在答错或者漏答情况下自动生效，每场仅限一张</div>
    </div>

     <div>
      <mu-dialog :open="dialog" >
          答错啦，您可以继续观战助力好友答题哦
        <mu-flat-button label="确定" slot="actions" primary @click="close"/>
      </mu-dialog>
    </div>

    <div>
      <mu-dialog :open="resurrectionDialog" >
          已自动为您使用复活卡，您可以继续参与答题
      </mu-dialog>
    </div>

  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false,
      resurrectionDialog: false,
      openid: '',
      time: 0,
      qIndex: 1,
      question: '答题即将开始',
      firstAnswer: '',
      secondAnswer: '',
      thirdAnswer: '',
      interval: null,
      isContinue: true,
      number: 0,
      showAnswer: false,
      getQuestionTimeout: null,
      notRight: false,
      selectIndex: 0,
      resurrection: 0,
      resurrected: false,
      showResurrected: true
    }
  },
  methods: {
    async answerFun (answer) {
      if (!this.isContinue) {
        console.log('--------------不能回答--------------------')
        return
      }
      this.selectIndex = answer
      let res = await this.$http.post(this.$url + '/api/game/answer', {
        qIndex: this.qIndex,
        answer: answer,
        openid: this.openid
      })
      console.log('----------------------------', res.data)
      if (res.data.code === 0) {
        console.log('------------------回答正确---------------')
        this.isContinue = true
      } else if (res.data.code === -1) {
        console.log('------------------回答错误---------------')
        this.isContinue = false
        this.notRight = true
      } else if (res.data.code === 2) {
        this.resurrected = true
        this.isContinue = true
      }
    },
    async setIQuestion (msg) {
      this.selectIndex = 0
      clearInterval(this.interval)
      this.qIndex = msg.index
      this.question = msg.question,
      this.firstAnswer = msg.firstAnswer,
      this.secondAnswer = msg.secondAnswer,
      this.thirdAnswer = msg.thirdAnswer
      this.time = 7
      this.interval = setInterval(() => {
       if (this.time > 0) {
         this.time = this.time -1
       } else {
        if (this.selectIndex === 0 && this.resurrection > 0 && this.showResurrected) {
          this.resurrectionDialog = true
          this.resurrection = this.resurrection - 1
          this.resurrected = false
          this.showResurrected = false
          setTimeout(() => {
            this.resurrectionDialog = false
          }, 2500)
        } else if (this.selectIndex === 0 && this.isContinue) {
          this.dialog = true
          this.isContinue = false
        }
        this.question = '正确答案'
        clearInterval(this.interval)
        // 防止未收到消息倒计时13秒
        this.getQuestionTimeout = setTimeout(async() => {
          if (this.time === 0) {
            // await this.getQuestion()
          }
        }, 1000 * 13)
       }
      }, 1000)
    },
    async getQuestion () {
      // 请求当前题目和答案
      let opt = {
        method: 'get',
        url: this.$url + '/api/game/question'
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.qIndex = res.data.data.index
        this.question = res.data.data.question,
        this.firstAnswer = res.data.data.firstAnswer,
        this.secondAnswer = res.data.data.secondAnswer,
        this.thirdAnswer = res.data.data.thirdAnswer 
        this.time = 4
        this.interval = setInterval(() => {
          if (this.time > 0) {
            this.time = this.time -1
          }    
        }, 1000)             
      }
    },
    async logout() {
      this.$socket.emit('logout', {openid: this.openid}, ack => {
        this.$router.push({name: 'index'})
      })
    },
    close () {
      this.dialog = false
    }
  },
  async created () {
    this.openid = this.$route.params.openid
    // 获取复活卡
    this.$socket.emit('resurrection', {openid: this.openid}, ack => {
      this.resurrection = ack
    })
    // 跳转到答题页面
    this.$socket.on('question', msg => {
      console.log('-----------msg--------', msg)
      this.setIQuestion(msg)
    })
    this.$socket.on('answer', msg => {
      if (msg.answer === 1) {
        this.answerStr = this.firstAnswer
      } else if (msg.answer === 2) {
        this.answerStr = this.secondAnswer
      } else {
        this.answerStr = this.thirdAnswer
      }
      this.question = ''
      this.showAnswer = true
      if (this.notRight && !this.isContinue) {
        this.dialog = true
        this.notRight = false
      } else if (this.resurrected) {
        this.resurrection = this.resurrection - 1
        this.resurrectionDialog = true
        this.resurrected = false
        setTimeout(() => {
          this.resurrectionDialog = false
        }, 2500)
      }
    })
    this.$socket.on('success', msg => {
      if (this.isContinue) {
        this.$router.push({ name: 'success', params: { gameId: msg.gameId }})
      } else {
        this.$router.push({ name: 'index'})
      }
    })
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #content {
    width: 80%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 50px;
    height: 300px;
    border-radius: 8px;
    position: relative;
    padding-top: 60px;
    box-shadow: 0px 9px 15px #888888;
  }
  #sen {
    position: absolute;
    border-radius: 50%;
    width: 80px;
    height: 80px;
    background-color: #1E1344;
    margin: auto; 
    right: 0;
    left: 0;
    top: -30px;
  }
  #time {
    position: absolute;
    top: 3px;
    left: 3px;
    right: 3px;
    bottom: 3px;
    border: 4px solid #F8306A;
    border-radius: 50%;
    color: #fff;
    font-weight: bold;
    font-size: 40px;
    line-height: 70px;
  }
  #question {
    width: 100%;
    text-align: center;
    font-size: 16px;
    height: 40px;
    line-height: 24px;
    color: #fff;
  }
  #answer_div {
    width: 100%;
    margin-top: 0px;
  }
  #answer_div .tab {
    background-color: #4B4269;
    width: 80%;
    text-align: center;
    margin:0 auto;
    height: 30px;
    margin-top: 28px;
    border-radius: 18px;
    line-height: 30px;
    font-size: 18px;
    color: #fff;
  }
  .active {
    background-color: #00FFFF !important;
  }
  #btn {
    width: 100px;
    height: 35px;
    margin:0 auto;
    background-color: #1E1344;
    color: #fff;
    text-align: center;
    font-size: 18px;
    border-radius: 18px;
    margin-top: 75px;
    line-height: 35px;
  }
  #fh {
    margin-top: 20px;
    width: 100%;
    text-align: center;
    height: 30px;
    line-height: 30px;
    font-size: 18px;
    color: #1E1344;
    font-weight: 600;
  }
  #fh img {
    width: 29px;
    height: 29px;
  }
  #tip {
    margin-top: 15px;
    font-size: 16px;
    color: #666;
    width: 100%;
    text-align: center;
  }
</style>



