<template>
  <div class="answer" id="answer">
    <div id="content">
      <div id="title">复活卡</div>
      <div id="bonus">{{user.resurrection}}</div>
      <img src="../assets/fh.png"> 
    </div>
    <router-link to="/code"><div id="btn_tx">分享邀请好友拿复活卡</div></router-link>
    <div id="tip">每邀请5位好友关注微信公众号即可获得复活卡</div>
    <div id="list">
      <div id="item" v-for="(item, index) in resurrection":key="index">
        <div id="left">
          <span style="vertical-align: super;">{{item.invatedTime|formatTimeContent }}</span>
        </div>
        <div id="middle">邀请满足</div>
        <div id="right">复活卡加1</div>
      </div>
    </div>
    <div id="btn"><router-link to="/center">返回个人中心</router-link></div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      user: {},
      resurrection: []
    }
  },
  methods: {
    async getUserInfo () {
      let opt = {
        method: 'get',
        url: this.$url + '/api/user',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      if (res.data.code === 0) {
        this.user = res.data.data
      }
    },
    async getResurrection () {
      let opt = {
        method: 'get',
        url: this.$url + '/api/user/resurrection',
        params: {
          openid: sessionStorage.getItem('openid')
        }
      }
      let res = await this.$http(opt)
      console.log('==================', res)
      if (res.data.code === 0) {
        this.resurrection = res.data.data 
      }
    }
  },
  async created () {
    this.getUserInfo()
    this.getResurrection()
  },
  filters: {
    formatTimeContent (val) {
      if (val) {
        if (val.toString().length < 13) {
          val = val * 1000
        }
        let time = new Date(val).toLocaleString(undefined, {hour12: false})
        // 把时间表达式中/改为-
        time = time.split('/').join('-')
        return time
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #answer {
    padding: 15px 15px 0 15px;
    height: 100%;
    position:relative;
  }
  #content {
    width: 100%;
    margin:0 auto;
    background-color: #1E1344;
    margin-top: 10px;
    height: 120px;
    border-radius: 8px;
    position: relative;
    padding-top: 10px;
  }
 #content img {
  position: absolute;
  width: 29px;
  height: 29px;
  left: 20px;
  top: 45px;
 }
 #title {
  font-size: 16px;
  color: #fff;
 }
 #bonus {
  font-size: 36px;
  color: #FB306B;
 }
 #btn_tx {
  width: 226px;
  height: 28px;
  margin:0 auto;
  margin-top: 20px;
  border-radius: 20px;
  color: #fff;
  font-size: 14px;
  line-height: 28px;
  text-align: center;
  background-color: #FB306B;
 }
 #tip {
  width: 100%;
  text-align: center;
  color: #6B6B6B;
  margin-top: 10px;
  font-weight: bold;
 }
 #list {
  overflow-y: auto;
  position: absolute;
  top: 235px;
  bottom: 60px;
  left: 15px;
  right: 15px;
 }
 #item {
  height: 50px;
  width: 100%;
  position: relative;
  border-bottom: 2px solid #f0f0f0;
 }
 #left {
  float: left;
  text-align: left;
  color: #737373;
  padding: 10px 5px 5px 5px;
  padding-right: 35px;
 }
 #middle {
  float: left;
  padding: 10px 5px 5px 5px;
  color: #737373;
 }
 #right {
  position: absolute;
  right: 0;
  top: 0;
  bottom: 0;
  text-align: right;
  color: #737373;
  padding: 10px 5px 5px 5px;
 }
#btn {
  width: 150px;
  height: 35px;
  margin:0 auto;
  background-color: #1E1344;
  color: #fff;
  text-align: center;
  font-size: 16px;
  border-radius: 18px;
  margin-top: 75px;
  line-height: 35px;
  position: absolute;
  margin: auto; 
  right: 0;
  left: 0;
  bottom: 10px;
}
#btn a {
  color: #fff !important;
}
</style>



