<template>
  <div class="success" id="success">
    <div id="content">
      <img src="../assets/success.png">
      <p id="tip">恭喜您，闯关成功！</p>
      <p id="su">系统正在为您计算所获奖金，请耐心等待</p>
      <p id="su">10S后进行跳转</p>
      <p id="su">结果即将揭晓，敬请等待</p>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      dialog: false
    }
  },
  methods: {
    open () { 
      this.dialog = true
    },
    close () {
      this.dialog = false
    }
  },
  async created () {
    console.log('--------------------success-----------------',  this.$route.params.code)
    this.$socket.on('result', msg => {
      this.$router.push({ name: 'result', params: { gameId: msg.gameId }})
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  #success {
    padding: 15px 15px 0 15px;
    height: 100%;
    position: relative;
  }
  #content {
    width: 100%;
    height: 80px;
    margin:0 auto;
    margin-top: 50px;
    text-align: center;
  }
  #content img {
    width: 77px;
    height: 100%;
  }
  #tip {
    color: #1E1344;
    font-size: 18px;
    margin-top: 14px;
  }
  #su {
    color: #989898;
    font-weight: 400;
  }
</style>



